import React, { Suspense } from 'react';

import './scss/app.scss';
import Home from './pages/Home';

import { Routes, Route } from 'react-router-dom';
import MainLayout from './layouts/MainLayout';

const Cart = React.lazy(() => import(/* webpackChunkName: "Cart"*/ './pages/Cart'));
const FullParfum = React.lazy(() => import(/* webpackChunkName: "FullParfum"*/ './pages/FullParfum'));
const NotFound = React.lazy(() => import(/* webpackChunkName: "NotFound"*/ './pages/NotFound'));

function App() {

  return (
    <Routes>
      <Route path="/" element={<MainLayout />}>
        <Route path="/" element={<Home />} />
        <Route path="/cart" element={

          <Suspense fallback={<div>Завантаження...</div>}>
            <Cart />
          </Suspense>
        } />

        <Route path="/parfum/:id" element={<Suspense fallback={<div>Завантаження...</div>}>
          <FullParfum />
        </Suspense>
        } />

        <Route path="*" element={<Suspense fallback={<div>Завантаження...</div>}>
          <NotFound />
        </Suspense>
        } />
      </Route>
    </Routes>
  );
}

export default App;
